/* Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro Display", "Segoe UI", "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  color: #333;
  line-height: 1.6;
  min-height: 100vh;
}

/* Portfolio Container */
.portfolio-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 1.5rem 1rem;
}

.portfolio-content {
  position: relative;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 24px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.1),
    0 8px 16px rgba(0, 0, 0, 0.05);
  padding: 2.5rem;
  max-width: 900px;
  width: 100%;
  margin: 0 auto;
  z-index: 1;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.portfolio-content::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 24px;
  padding: 2px;
  background: linear-gradient(120deg, #5e5ce6, #6a82fb, #a259c6, #f06292, #3a8dde, #5e5ce6, #6a82fb, #f06292);
  background-size: 400% 400%;
  z-index: -1;
  pointer-events: none;
  animation: modern-glass-gradient 8s linear infinite;
  opacity: 0.8;
  -webkit-mask:
    linear-gradient(#fff 0 0) content-box, 
    linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  box-sizing: border-box;
}

@keyframes modern-glass-gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Profile Section */
.profile-section {
  text-align: center;
  margin-bottom: 2.5rem;
  position: relative;
}

.profile-image-container {
  display: flex;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.profile-image-container.small {
  margin-bottom: 1rem;
}

.profile-name {
  font-size: 2.2rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
  letter-spacing: -0.02em;
  transition: transform 0.35s cubic-bezier(0.4, 0, 0.2, 1);
  line-height: 1.2;
}

.profile-name:hover {
  transform: scale(1.02);
  color: #1a1a1a;
}

.profile-title {
  font-size: 1.1rem;
  color: #666;
  font-weight: 400;
  letter-spacing: 0.5px;
  margin-bottom: 0.5rem;
}

/* Navigation Sections */
.navigation-sections {
  margin-bottom: 2.5rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.section-item.section-link {
  display: flex;
  align-items: flex-start;
  padding: 1.25rem;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0,0,0,0.04);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin: 0;
}

.section-item.section-link:hover {
  box-shadow: 0 8px 32px rgba(102,126,234,0.12);
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.95);
}

.section-content {
  flex: 1;
  text-align: left;
}

.section-preview {
  color: #555;
  line-height: 1.6;
  margin: 0;
  transition: color 0.3s ease;
  font-size: 0.95rem;
}

.section-item:hover .section-preview {
  color: #333;
}

/* Social Links */
.social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2.5rem;
  flex-wrap: wrap;
}

.social-btn {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  padding: 0.75rem 1.25rem;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 12px;
  text-decoration: none;
  color: #333;
  font-weight: 500;
  font-size: 0.9rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.04);
}

.social-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.4), transparent);
  transition: left 0.5s;
}

.social-btn:hover::before {
  left: 100%;
}

.social-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.95);
}

.social-btn:hover .social-icon {
  animation: social-icon-bounce 0.6s ease;
}

@keyframes shine-gold {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.social-btn > * {
  position: relative;
  z-index: 1;
}

@keyframes social-icon-bounce {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.social-icon {
  font-size: 1.1rem;
  transition: transform 0.3s ease;
}

/* Footer */
.footer {
  text-align: center;
  padding-top: 1.5rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  margin-top: 2rem;
}

.footer-signature {
  font-size: 0.9rem;
  color: #666;
  font-weight: 500;
  letter-spacing: 0.5px;
}

/* Page Header */
.page-header {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.back-link {
  color: #667eea;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  transition: color 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.back-link:hover {
  color: #5a67d8;
  text-decoration: underline;
}

.page-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0;
}

.page-content {
  margin-bottom: 2rem;
}

.about-content {
  display: flex;
  gap: 2rem;
  align-items: flex-start;
}

.about-text {
  flex: 1;
}

.content-title {
  font-size: 1.8rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1rem;
  line-height: 1.3;
}

.content-description {
  color: #555;
  line-height: 1.7;
  margin-bottom: 1.5rem;
  font-size: 1rem;
}

.contact-info h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1rem;
}

.contact-links {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.contact-link {
  color: #667eea;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.contact-link:hover {
  color: #5a67d8;
  text-decoration: underline;
}

/* Work Experience Styles */
.work-experience {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}

.work-item {
  border-left: 3px solid #667eea;
  padding-left: 2rem;
  position: relative;
}

.work-item::before {
  content: '';
  position: absolute;
  left: -6px;
  top: 0;
  width: 12px;
  height: 12px;
  background-color: #667eea;
  border-radius: 50%;
}

.work-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.job-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0;
}

.job-duration {
  font-size: 0.9rem;
  color: #888;
  font-weight: 500;
  white-space: nowrap;
}

.company-name {
  font-size: 1.1rem;
  font-weight: 500;
  color: #667eea;
  margin: 0 0 1rem 0;
}

.job-description {
  color: #555;
  line-height: 1.6;
}

.job-point {
  margin-bottom: 0.5rem;
  padding-left: 0.5rem;
}

/* Education Styles */
.education-content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.education-item {
  border-left: 3px solid #764ba2;
  padding-left: 2rem;
  position: relative;
  margin-bottom: 2rem;
}

.education-item::before {
  content: '';
  position: absolute;
  left: -6px;
  top: 0;
  width: 12px;
  height: 12px;
  background-color: #764ba2;
  border-radius: 50%;
}

.education-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.degree-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #1a1a1a;
  margin: 0;
}

.education-duration {
  font-size: 0.9rem;
  color: #888;
  font-weight: 500;
  white-space: nowrap;
}

.school-name {
  font-size: 1.1rem;
  font-weight: 500;
  color: #764ba2;
  margin: 0 0 0.5rem 0;
}

.gpa {
  font-size: 0.9rem;
  color: #666;
  font-weight: 500;
}

.education-summary {
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: #f8f9fa;
  border-radius: 12px;
  border-left: 4px solid #764ba2;
}

.education-summary h3 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1rem;
}

.education-summary p {
  color: #555;
  line-height: 1.6;
  margin: 0;
}

/* Skills Styles */
.skills-content {
  width: 100%;
}

.skills-grid {
  display: grid;
  gap: 2rem;
}

.skill-category {
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 12px;
  border-left: 4px solid #667eea;
  transition: all 0.3s ease;
}

.skill-category:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
}

.category-title {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1rem;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.skill-tag {
  display: inline-block;
  padding: 0.4rem 0.8rem;
  background-color: white;
  border: 1px solid #e0e0e0;
  border-radius: 20px;
  font-size: 0.85rem;
  color: #555;
  font-weight: 500;
  transition: all 0.3s ease;
}

.skill-tag:hover {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-color: transparent;
  transform: translateY(-1px);
}

/* Projects - Bento Grid Styles */
.projects-content {
  width: 100%;
}

.bento-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  grid-auto-rows: auto;
}

.bento-item {
  background-color: white;
  border-radius: 16px;
  padding: 2rem;
  border: 1px solid #e8e9ea;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.bento-item.featured {
  grid-column: span 2;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.02) 0%, rgba(118, 75, 162, 0.02) 100%);
}

.bento-item.small {
  grid-column: span 1;
  min-height: 200px;
}

.bento-item.medium {
  grid-column: span 1;
  min-height: 250px;
}

.bento-item:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 40px rgba(0, 0, 0, 0.1);
  border-color: #667eea;
}

.project-header {
  margin-bottom: 1.5rem;
}

.project-title {
  font-size: 1.4rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 1rem;
  letter-spacing: -0.01em;
}

.project-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.tech-tag {
  padding: 0.3rem 0.8rem;
  background-color: #f0f2ff;
  color: #667eea;
  border-radius: 16px;
  font-size: 0.8rem;
  font-weight: 500;
}

.project-description {
  color: #555;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.project-point {
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.project-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-category-badge {
  padding: 0.4rem 1rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* Special Bento Cards */
.info-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.info-content h4 {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.info-content p {
  font-size: 0.9rem;
  opacity: 0.9;
}

.stats-card {
  background: linear-gradient(135deg, #764ba2 0%, #667eea 100%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.stats-content {
  text-align: center;
}

.stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
}

.stat:last-child {
  margin-bottom: 0;
}

.stat-number {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.stat-label {
  font-size: 0.8rem;
  opacity: 0.9;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.contact-card {
  background-color: #f8f9fa;
  border: 2px dashed #d0d7de;
  text-align: center;
}

.contact-content h4 {
  font-size: 1.2rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.contact-content p {
  color: #555;
  margin-bottom: 1.5rem;
  font-size: 0.9rem;
}

.contact-buttons {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.contact-btn {
  padding: 0.5rem 1rem;
  background-color: #667eea;
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-size: 0.85rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.contact-btn:hover {
  background-color: #764ba2;
  transform: translateY(-1px);
}

/* Modern Section Cards */
.modern-section-card {
  display: flex;
  align-items: flex-start;
  padding: 1.25rem;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0,0,0,0.04);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  text-decoration: none;
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.3);
  margin: 0;
}

.modern-section-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #667eea, #764ba2, #f093fb);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.modern-section-card:hover::before {
  transform: scaleX(1);
}

.modern-section-card:hover {
  box-shadow: 0 8px 32px rgba(102,126,234,0.12);
  transform: translateY(-2px);
  background: rgba(255, 255, 255, 0.95);
}

.section-accent {
  width: 4px;
  height: 100%;
  border-radius: 2px;
  margin-right: 1rem;
  flex-shrink: 0;
}

.section-main {
  flex: 1;
}

.section-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 0.5rem;
}

.section-preview {
  color: #555;
  line-height: 1.6;
  margin: 0;
  font-size: 0.9rem;
}

.about-teaser-link {
  color: #667eea;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease;
}

.about-teaser-link:hover {
  color: #5a67d8;
  text-decoration: underline;
}

/* Responsive Design */
@media (max-width: 768px) {
  .portfolio-container {
    padding: 1rem 0.5rem;
  }
  
  .portfolio-content {
    padding: 1.5rem;
    border-radius: 20px;
  }
  
  .portfolio-content::before {
    border-radius: 20px;
  }
  
  .profile-name {
    font-size: 1.8rem;
  }
  
  .section-item {
    padding: 1rem;
  }
  
  .section-link {
    padding: 1rem;
  }
  
  .work-header,
  .education-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
  
  .social-links {
    justify-content: center;
    gap: 0.75rem;
  }
  
  .social-btn {
    padding: 0.6rem 1rem;
    font-size: 0.85rem;
  }
  
  .footer {
    margin-top: 1.5rem;
  }
  
  .bento-grid {
    grid-template-columns: 1fr;
  }
  
  .bento-item.featured {
    grid-column: 1;
  }
  
  .skills-grid {
    grid-template-columns: 1fr;
  }
  
  .modern-section-card {
    padding: 1rem;
  }
  
  .section-accent {
    width: 3px;
    margin-right: 0.75rem;
  }
  
  .section-main {
    flex: 1;
  }
  
  .section-title {
    font-size: 1rem;
  }
  
  .section-preview {
    font-size: 0.85rem;
  }
  
  .portfolio-gradient-border {
    padding: 2px;
  }
  
  .portfolio-gradient-border > .portfolio-content {
    border-radius: 18px;
  }
  
  .about-content {
    flex-direction: column;
    gap: 1.5rem;
  }
}

@media (max-width: 480px) {
  .portfolio-content {
    padding: 1rem;
    border-radius: 16px;
  }
  
  .profile-name {
    font-size: 1.6rem;
  }
  
  .page-title {
    font-size: 1.3rem;
  }
  
  .work-item,
  .education-item {
    padding: 1rem;
    margin-bottom: 1rem;
  }
}