.profile-image-3d-container {
  perspective: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-wrapper {
  position: relative;
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  cursor: pointer;
}

.image-wrapper:hover {
  transform: rotateY(15deg) rotateX(10deg) scale(1.05);
}

.main-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 24px;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.15),
    0 10px 20px rgba(0, 0, 0, 0.1),
    0 4px 8px rgba(0, 0, 0, 0.05);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  z-index: 2;
}

.image-wrapper:hover .main-image {
  box-shadow: 
    0 30px 60px rgba(0, 0, 0, 0.25),
    0 15px 30px rgba(0, 0, 0, 0.15),
    0 8px 16px rgba(0, 0, 0, 0.1);
}

.profile-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 24px;
  color: white;
  font-size: 2rem;
  font-weight: bold;
  box-shadow: 
    0 20px 40px rgba(0, 0, 0, 0.15),
    0 10px 20px rgba(0, 0, 0, 0.1),
    0 4px 8px rgba(0, 0, 0, 0.05);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  z-index: 2;
}

.image-wrapper:hover .profile-placeholder {
  box-shadow: 
    0 30px 60px rgba(0, 0, 0, 0.25),
    0 15px 30px rgba(0, 0, 0, 0.15),
    0 8px 16px rgba(0, 0, 0, 0.1);
}

.depth-layer {
  position: absolute;
  top: 8px;
  left: 8px;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.08) 0%, rgba(255, 255, 255, 0.03) 100%);
  border-radius: 24px;
  transform: translateZ(-16px);
  z-index: 1;
}

.depth-layer-2 {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.04) 0%, rgba(255, 255, 255, 0.01) 100%);
  border-radius: 24px;
  transform: translateZ(-32px);
  z-index: 0;
}

.highlight {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.15) 0%, transparent 50%);
  border-radius: 24px;
  opacity: 0;
  transition: opacity 0.6s ease;
  z-index: 3;
  pointer-events: none;
}

.image-wrapper:hover .highlight {
  opacity: 1;
}

.frame {
  position: absolute;
  top: -8px;
  left: -8px;
  width: calc(100% + 16px);
  height: calc(100% + 16px);
  border: 2px solid rgba(255, 255, 255, 0.08);
  border-radius: 28px;
  transform: translateZ(-8px);
  transition: all 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.image-wrapper:hover .frame {
  border-color: rgba(255, 255, 255, 0.2);
  transform: translateZ(-8px) scale(1.02);
}

/* Modern size variations - More user-friendly proportions */
.profile-image-3d-large .image-wrapper {
  width: 280px;
  height: 350px; /* Better aspect ratio for portraits */
}

.profile-image-3d-medium .image-wrapper {
  width: 220px;
  height: 275px;
}

.profile-image-3d-small .image-wrapper {
  width: 160px;
  height: 200px;
}

.profile-image-3d-small .profile-placeholder {
  font-size: 1.4rem;
}

.profile-image-3d-medium .profile-placeholder {
  font-size: 1.6rem;
}

.profile-image-3d-large .profile-placeholder {
  font-size: 1.8rem;
}

/* Subtle floating animation - More refined */
@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-8px); }
}

.image-wrapper {
  animation: float 8s ease-in-out infinite;
}

/* Enhanced responsive design with modern breakpoints */
@media (max-width: 1024px) {
  .profile-image-3d-large .image-wrapper {
    width: 240px;
    height: 300px;
  }
  
  .profile-image-3d-medium .image-wrapper {
    width: 200px;
    height: 250px;
  }
  
  .profile-image-3d-small .image-wrapper {
    width: 140px;
    height: 175px;
  }
}

@media (max-width: 768px) {
  .profile-image-3d-large .image-wrapper {
    width: 200px;
    height: 250px;
  }
  
  .profile-image-3d-medium .image-wrapper {
    width: 180px;
    height: 225px;
  }
  
  .profile-image-3d-small .image-wrapper {
    width: 120px;
    height: 150px;
  }
}

@media (max-width: 480px) {
  .profile-image-3d-large .image-wrapper {
    width: 160px;
    height: 200px;
  }
  
  .profile-image-3d-medium .image-wrapper {
    width: 140px;
    height: 175px;
  }
  
  .profile-image-3d-small .image-wrapper {
    width: 100px;
    height: 125px;
  }
}

/* Modern glassmorphism effect for container */
.profile-image-3d-container {
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

/* Smooth transitions for all interactive elements */
.image-wrapper * {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
} 